var baseClasses = function baseClasses() {
  return {
    container: 'animated fadeIn dark z-1 db w-100 center pv8 ph3 tc',
    // container: 'tc animated fadeIn ph3 dark z-1 db w-100 center pb3 pt8 fixed lelf-0 overflow-y-scroll', 
    wrapper: 'pt6'
  };
};

// EventBus
exports.default = {
  name: 'headerDrawer',
  mixins: [],
  props: {
    openState: { type: String, default: 'closed' },
    theme: { type: String, default: "light" }
  },
  components: {},
  data: function data() {
    return {
      DEBUG: true,
      c: baseClasses(),
      isOpen: false,
      headerHeight: '',
      state: {
        activeSlot: 'main-menu'
      },
      selectors: {
        header: '#view--header',
        pageContainer: '#PageContainer',
        footerSection: '#shopify-section-layout--footer'
      }
    };
  },
  created: function created() {
    _eventBus.EventBus.$on('header-drawer-open', this.toggleDrawer);
    // Mini cart.
    // EventBus.$on('productAddToCart:itemAddedShowMiniCart', this.toggleDrawer);
  },
  mounted: function mounted() {},

  watch: {},
  computed: {},
  methods: {
    getHeight: function getHeight(element) {
      /** Utility class that gets an element's height */
      var elementHeight = document.querySelector(element);
      return elementHeight.offsetHeight;
    },
    showPageContent: function showPageContent() {
      var pageContentEl = document.querySelector(this.selectors.pageContainer);
      this.log('showing pageContent');
      pageContentEl.classList.remove('animated');
      pageContentEl.classList.remove('fadeOut');
      pageContentEl.classList.remove('a3');
      pageContentEl.style.height = '100%';
    },
    hidePageContent: function hidePageContent() {
      var pageContentEl = document.querySelector(this.selectors.pageContainer);
      this.log('hiding pageContent');
      pageContentEl.classList.add('animated');
      pageContentEl.classList.add('fadeOut');
      pageContentEl.classList.add('a3');
      // pageContentEl.style.height = (Math.max(document.documentElement.clientHeight, window.innerHeight || 0) - this.getHeight(this.selectors.header) - this.getHeight(this.selectors.footerSection)) + 'px';
      pageContentEl.style.height = '0px';
      // pageContentEl.style.height = '100' + 'vh';
    },
    toggleDrawer: function toggleDrawer(action) {
      if (action.shouldOpen) {
        // open drawer positioned at the bottom of the header
        this.headerHeight = '6rem'; // this.getHeight(this.selectors.header) + 'px';

        // Hide page content
        this.hidePageContent();

        // Show bg overlay
        //this.showBackgroundOverlay();
        _eventBus.EventBus.$emit('background:showOverlay', true);
        // EventBus.$emit('background:reloadParticles');

        this.isOpen = true;

        //I'm using "click" but it works with any event
        // document.addEventListener('click', this.closeOnOutsideClicks )


        // Emit so that the menu-icon state can be updated when closed by clicking outside the drawer.
        _eventBus.EventBus.$emit('header-drawer-isopen', true);
      } else {
        // hide bg overlay
        // this.hideBackgroundOverlay();
        _eventBus.EventBus.$emit('background:showOverlay', false);

        // Show page content
        this.showPageContent();

        this.isOpen = false;

        // document.removeEventListener("click", this.closeOnOutsideClicks);

        // Emit so that the menu-icon state can be updated when closed by clicking outside the drawer.
        _eventBus.EventBus.$emit('header-drawer-isopen', false);
      }

      // Assign the active slot.
      this.state.activeSlot = action.slot;
    },
    closeOnOutsideClicks: function closeOnOutsideClicks(event) {
      console.warn('closeOnOutsideClicks', event);
      var headerEl = document.getElementById('view--header');

      var isClickInsideHeader = headerEl.contains(event.target);
      console.warn('headerEl', headerEl);
      console.warn('isClickInsideHeader', isClickInsideHeader);

      if (isClickInsideHeader) {
        this.log('clicked inside. ignore it..');
      } else {
        //the click was outside the headerEl, close the drawer..
        this.log('closing drawer from outside the header + drawer.. click');
        this.toggleDrawer(false);
      }
    }
  }
};